import React,{useState, useEffect} from 'react'
import Notification from './components/Notification'
import Form from './components/Form'
import PersonForm from './components/PersonForm'
import Display from './components/Display'
import personService from './services/personService'

const App = () => {
  const [persons, setPersons] = useState([])
  const [newName, setNewName ] = useState('Enter Name here...')
  const [newNumber, setNewNumber] = useState('Enter Number here...');
  const [filter, setFilter] = useState('')
  const [notify, setNotify] = useState('')
  const [color , setColor] = useState('') 

  useEffect(() =>{
      personService
          .getAll()
          .then(response => setPersons(response.data))
  },[])

  const addPerson = (event) => {
      event.preventDefault();
      if(persons.some(person => person.name === newName)){
          const person = persons.find(person => person.name === newName)
          if(window.confirm(`${person.name} is already added to phonebook, replace the old number with a new one ?`)){

              const updatePerson = {...person, number : newNumber}

              personService
                  .update(person)
                  .then(response =>{
                      setPersons(persons.map(person => person.name !== newName ? person : updatePerson))
                      return response
                  })
                  .then((response) => {
                      setNotify(`Updated ${response.data.name}'s Number `)
                      setColor('green')
                      setTimeout(() => setNotify(''), 5000)
                  })
                  .catch(error => {
                      setNotify(`Information about ${person.name} has already been removed from server`)
                      setColor('red')
                      setPersons(persons.filter(p => p.id !== person.id))
                  })
          }
      }
      else{
          const newObject = {
              name : newName,
              number: newNumber,
              id: Math.floor(Math.random()*100)
          }

          personService
              .create(newObject)
              .then(response => {
                  setPersons(persons.concat(response.data))
                  setNewName('Enter Name Here...')
                  setNewNumber('Enter Number Here...')
                  return response
              })
              .then((response) => {
                  setNotify(`Added ${newObject.name}`)
                  setColor('green')
                  setTimeout(()=>setNotify(''), 5000)
              })
              .catch(error => {
                  console.log(error.response)
                  console.log(error.response.data)
              })
      }
  
  } 

  const handleFilterChange = (event) => {
      setFilter(event.target.value)
  }

  const handleNameChange = (event) => {
      setNewName(event.target.value)
  }

  const handleNumberChange = (event) => {
      setNewNumber(event.target.value)
  }

  const filterPersons = filter.length === 0 ? persons : persons.filter(person => person.name.includes(filter) === true)

  return (
    <div>
      <h2>Phonebook</h2>
      <Notification message={notify} color={color} />
      <Form  value={filter} onChange={handleFilterChange}/>
      <h2>Add a new </h2>
      <PersonForm addPerson={addPerson} newName={newName} handleNameChange={handleNameChange} newNumber={newNumber} handleNumberChange={handleNumberChange}  />
      <h2>Numbers</h2>
      <Display persons={filterPersons}/>
    </div>
  )
}

export default App
