import React,{useState} from 'react'
import Person from './Person'
import personService from '/home/abhishek/FullStack/part2/thephonebook/src/services/personService'

const Display = ({persons}) => {
    const [deletePerson, setDeletePerson] = useState({})

    const handleOnClick = (person) => {
        if(window.confirm(`Delete ${person.name}`)){
            console.log(`${person.name} will be deleted`)
            
            personService
                .remove(person)
                .then(response =>{
                    setDeletePerson(response.data)
                })
        }
    }

    const rows = () => persons.map(person =>  <Person key={person.id} person={person} handleOnClick={() => handleOnClick(person)}/>)
    return(
        <div>
            {rows()}
        </div>
    )
}

export default Display